module.exports = [{"id":"A","type":"part","chapter":"1","title":"INTRODUCTION AND AUTHORITY","content":"<h4 id='introductory-commentary' class='Commentary'><strong>Introductory Commentary</strong></h4><p>Subparts 1 and 2 of this Part provide an introduction to the Guidelines Manual describing the historical development and evolution of the federal sentencing guidelines. Subpart 1 sets forth the original introduction to the Guidelines Manual as it first appeared in 1987, with the inclusion of amendments made occasionally thereto between 1987 and 2000. The original introduction, as so amended, explained a number of policy decisions made by the United States Sentencing Commission (“Commission”) when it promulgated the initial set of guidelines and therefore provides a useful reference for contextual and historical purposes. Subpart 2 further describes the evolution of the federal sentencing guidelines after the initial guidelines were promulgated.</p><p>Subpart 3 of this Part states the authority of the Commission to promulgate federal sentencing guidelines, policy statements, and commentary.</p><h3 id='original-introduction-to-the-guidelines-manual'>1. Original Introduction to the Guidelines Manual</h3><p>The following provisions of this Subpart set forth the original introduction to this manual, effective November 1, 1987, and as amended through November 1, 2000:</p><blockquote><p><strong>1. Authority</strong></p><p>The United States Sentencing Commission (“Commission”) is an independent agency in the judicial branch composed of seven voting and two non-voting, <em>ex officio</em> members. Its principal purpose is to establish sentencing policies and practices for the federal criminal justice system that will assure the ends of justice by promulgating detailed guidelines prescribing the appropriate sentences for offenders convicted of federal crimes.</p><p>The guidelines and policy statements promulgated by the Commission are issued pursuant to Section 994(a) of Title 28, United States Code.</p><p><strong>2. The Statutory Mission</strong></p><p>The Sentencing Reform Act of 1984 (Title II of the Comprehensive Crime Control Act of 1984) provides for the development of guidelines that will further the basic purposes of criminal punishment: deterrence, incapacitation, just punishment, and rehabilitation. The Act delegates broad authority to the Commission to review and rationalize the federal sentencing process.</p><p>The Act contains detailed instructions as to how this determination should be made, the most important of which directs the Commission to create categories of offense behavior and offender characteristics. An offense behavior category might consist, for example, of “bank robbery/committed with a gun/$2500 taken.” An offender characteristic category might be “offender with one prior conviction not resulting in imprisonment.” The Commission is required to prescribe guideline ranges that specify an appropriate sentence for each class of convicted persons determined by coordinating the offense behavior categories with the offender characteristic categories. Where the guidelines call for imprisonment, the range must be narrow: the maximum of the range cannot exceed the minimum by more than the greater of 25 percent or six months. 28 U.S.C. § 994(b)(2).</p><p>Pursuant to the Act, the sentencing court must select a sentence from within the guideline range. If, however, a particular case presents atypical features, the Act allows the court to depart from the guidelines and sentence outside the prescribed range. In that case, the court must specify reasons for departure. 18 U.S.C. § 3553(b). If the court sentences within the guideline range, an appellate court may review the sentence to determine whether the guidelines were correctly applied. If the court departs from the guideline range, an appellate court may review the reasonableness of the departure. 18 U.S.C. § 3742. The Act also abolishes parole, and substantially reduces and restructures good behavior adjustments.</p><p>The Commission’s initial guidelines were submitted to Congress on April 13, 1987. After the prescribed period of Congressional review, the guidelines took effect on November 1, 1987, and apply to all offenses committed on or after that date. The Commission has the authority to submit guideline amendments each year to Congress between the beginning of a regular Congressional session and May 1. Such amendments automatically take effect 180 days after submission unless a law is enacted to the contrary. 28 U.S.C. § 994(p).</p><p>The initial sentencing guidelines and policy statements were developed after extensive hearings, deliberation, and consideration of substantial public comment. The Commission emphasizes, however, that it views the guideline-writing process as evolutionary. It expects, and the governing statute anticipates, that continuing research, experience, and analysis will result in modifications and revisions to the guidelines through submission of amendments to Congress. To this end, the Commission is established as a permanent agency to monitor sentencing practices in the federal courts.</p><p><strong>3. The Basic Approach (Policy Statement)</strong></p><p>To understand the guidelines and their underlying rationale, it is important to focus on the three objectives that Congress sought to achieve in enacting the Sentencing Reform Act of 1984. The Act’s basic objective was to enhance the ability of the criminal justice system to combat crime through an effective, fair sentencing system. To achieve this end, Congress first sought honesty in sentencing. It sought to avoid the confusion and implicit deception that arose out of the pre-guidelines sentencing system which required the court to impose an indeterminate sentence of imprisonment and empowered the parole commission to determine how much of the sentence an offender actually would serve in prison. This practice usually resulted in a substantial reduction in the effective length of the sentence imposed, with defendants often serving only about one-third of the sentence imposed by the court.</p><p>Second, Congress sought reasonable uniformity in sentencing by narrowing the wide disparity in sentences imposed for similar criminal offenses committed by similar offenders. Third, Congress sought proportionality in sentencing through a system that imposes appropriately different sentences for criminal conduct of differing severity.</p><p>Honesty is easy to achieve: the abolition of parole makes the sentence imposed by the court the sentence the offender will serve, less approximately fifteen percent for good behavior. There is a tension, however, between the mandate of uniformity and the mandate of proportionality. Simple uniformity — sentencing every offender to five years — destroys proportionality. Having only a few simple categories of crimes would make the guidelines uniform and easy to administer, but might lump together offenses that are different in important respects. For example, a single category for robbery that included armed and unarmed robberies, robberies with and without injuries, robberies of a few dollars and robberies of millions, would be far too broad.</p><p>A sentencing system tailored to fit every conceivable wrinkle of each case would quickly become unworkable and seriously compromise the certainty of punishment and its deterrent effect. For example: a bank robber with (or without) a gun, which the robber kept hidden (or brandished), might have frightened (or merely warned), injured seriously (or less seriously), tied up (or simply pushed) a guard, teller, or customer, at night (or at noon), in an effort to obtain money for other crimes (or for other purposes), in the company of a few (or many) other robbers, for the first (or fourth) time.</p><p>The list of potentially relevant features of criminal behavior is long; the fact that they can occur in multiple combinations means that the list of possible permutations of factors is virtually endless. The appropriate relationships among these different factors are exceedingly difficult to establish, for they are often context specific. Sentencing courts do not treat the occurrence of a simple bruise identically in all cases, irrespective of whether that bruise occurred in the context of a bank robbery or in the context of a breach of peace. This is so, in part, because the risk that such a harm will occur differs depending on the underlying offense with which it is connected; and also because, in part, the relationship between punishment and multiple harms is not simply additive. The relation varies depending on how much other harm has occurred. Thus, it would not be proper to assign points for each kind of harm and simply add them up, irrespective of context and total amounts.</p><p>The larger the number of subcategories of offense and offender characteristics included in the guidelines, the greater the complexity and the less workable the system. Moreover, complex combinations of offense and offender characteristics would apply and interact in unforeseen ways to unforeseen situations, thus failing to cure the unfairness of a simple, broad category system. Finally, and perhaps most importantly, probation officers and courts, in applying a complex system having numerous subcategories, would be required to make a host of decisions regarding whether the underlying facts were sufficient to bring the case within a particular subcategory. The greater the number of decisions required and the greater their complexity, the greater the risk that different courts would apply the guidelines differently to situations that, in fact, are similar, thereby reintroducing the very disparity that the guidelines were designed to reduce.</p><p>In view of the arguments, it would have been tempting to retreat to the simple, broad category approach and to grant courts the discretion to select the proper point along a broad sentencing range. Granting such broad discretion, however, would have risked correspondingly broad disparity in sentencing, for different courts may exercise their discretionary powers in different ways. Such an approach would have risked a return to the wide disparity that Congress established the Commission to reduce and would have been contrary to the Commission’s mandate set forth in the Sentencing Reform Act of 1984.</p><p>In the end, there was no completely satisfying solution to this problem. The Commission had to balance the comparative virtues and vices of broad, simple categorization and detailed, complex subcategorization, and within the constraints established by that balance, minimize the discretionary powers of the sentencing court. Any system will, to a degree, enjoy the benefits and suffer from the drawbacks of each approach.</p><p>A philosophical problem arose when the Commission attempted to reconcile the differing perceptions of the purposes of criminal punishment. Most observers of the criminal law agree that the ultimate aim of the law itself, and of punishment in particular, is the control of crime. Beyond this point, however, the consensus seems to break down. Some argue that appropriate punishment should be defined primarily on the basis of the principle of “just deserts.” Under this principle, punishment should be scaled to the offender’s culpability and the resulting harms. Others argue that punishment should be imposed primarily on the basis of practical “crime control” considerations. This theory calls for sentences that most effectively lessen the likelihood of future crime, either by deterring others or incapacitating the defendant.</p><p>Adherents of each of these points of view urged the Commission to choose between them and accord one primacy over the other. As a practical matter, however, this choice was unnecessary because in most sentencing decisions the application of either philosophy will produce the same or similar results.</p><p>In its initial set of guidelines, the Commission sought to solve both the practical and philosophical problems of developing a coherent sentencing system by taking an empirical approach that used as a starting point data estimating pre-guidelines sentencing practice. It analyzed data drawn from 10,000 presentence investigations, the differing elements of various crimes as distinguished in substantive criminal statutes, the United States Parole Commission’s guidelines and statistics, and data from other relevant sources in order to determine which distinctions were important in pre-guidelines practice. After consideration, the Commission accepted, modified, or rationalized these distinctions.</p><p>This empirical approach helped the Commission resolve its practical problem by defining a list of relevant distinctions that, although of considerable length, was short enough to create a manageable set of guidelines. Existing categories are relatively broad and omit distinctions that some may believe important, yet they include most of the major distinctions that statutes and data suggest made a significant difference in sentencing decisions. Relevant distinctions not reflected in the guidelines probably will occur rarely and sentencing courts may take such unusual cases into account by departing from the guidelines.</p><p>The Commission’s empirical approach also helped resolve its philosophical dilemma. Those who adhere to a just deserts philosophy may concede that the lack of consensus might make it difficult to say exactly what punishment is deserved for a particular crime. Likewise, those who subscribe to a philosophy of crime control may acknowledge that the lack of sufficient data might make it difficult to determine exactly the punishment that will best prevent that crime. Both groups might therefore recognize the wisdom of looking to those distinctions that judges and legislators have, in fact, made over the course of time. These established distinctions are ones that the community believes, or has found over time, to be important from either a just deserts or crime control perspective.</p><p>The Commission did not simply copy estimates of pre-guidelines practice as revealed by the data, even though establishing offense values on this basis would help eliminate disparity because the data represent averages. Rather, it departed from the data at different points for various important reasons. Congressional statutes, for example, suggested or required departure, as in the case of the Anti-Drug Abuse Act of 1986 that imposed increased and mandatory minimum sentences. In addition, the data revealed inconsistencies in treatment, such as punishing economic crime less severely than other apparently equivalent behavior.</p><p>Despite these policy-oriented departures from pre-guidelines practice, the guidelines represent an approach that begins with, and builds upon, empirical data. The guidelines will not please those who wish the Commission to adopt a single philosophical theory and then work deductively to establish a simple and perfect set of categorizations and distinctions. The guidelines may prove acceptable, however, to those who seek more modest, incremental improvements in the status quo, who believe the best is often the enemy of the good, and who recognize that these guidelines are, as the Act contemplates, but the first step in an evolutionary process. After spending considerable time and resources exploring alternative approaches, the Commission developed these guidelines as a practical effort toward the achievement of a more honest, uniform, equitable, proportional, and therefore effective sentencing system.</p><p><strong>4. The Guidelines’ Resolution of Major Issues (Policy Statement)</strong></p><p>The guideline-drafting process required the Commission to resolve a host of important policy questions typically involving rather evenly balanced sets of competing considerations. As an aid to understanding the guidelines, this introduction briefly discusses several of those issues; commentary in the guidelines explains others.</p><p><strong>(a) Real Offense vs. Charge Offense Sentencing.</strong></p><p>One of the most important questions for the Commission to decide was whether to base sentences upon the actual conduct in which the defendant engaged regardless of the charges for which he was indicted or convicted (“real offense” sentencing), or upon the conduct that constitutes the elements of the offense for which the defendant was charged and of which he was convicted (“charge offense” sentencing). A bank robber, for example, might have used a gun, frightened bystanders, taken $50,000, injured a teller, refused to stop when ordered, and raced away damaging property during his escape. A pure real offense system would sentence on the basis of all identifiable conduct. A pure charge offense system would overlook some of the harms that did not constitute statutory elements of the offenses of which the defendant was convicted.</p><p>The Commission initially sought to develop a pure real offense system. After all, the pre-guidelines sentencing system was, in a sense, this type of system. The sentencing court and the parole commission took account of the conduct in which the defendant actually engaged, as determined in a presentence report, at the sentencing hearing, or before a parole commission hearing officer. The Commission’s initial efforts in this direction, carried out in the spring and early summer of 1986, proved unproductive, mostly for practical reasons. To make such a system work, even to formalize and rationalize the status quo, would have required the Commission to decide precisely which harms to take into account, how to add them up, and what kinds of procedures the courts should use to determine the presence or absence of disputed factual elements. The Commission found no practical way to combine and account for the large number of diverse harms arising in different circumstances; nor did it find a practical way to reconcile the need for a fair adjudicatory procedure with the need for a speedy sentencing process given the potential existence of hosts of adjudicated “real harm” facts in many typical cases. The effort proposed as a solution to these problems required the use of, for example, quadratic roots and other mathematical operations that the Commission considered too complex to be workable. In the Commission’s view, such a system risked return to wide disparity in sentencing practice.</p><p>In its initial set of guidelines submitted to Congress in April 1987, the Commission moved closer to a charge offense system. This system, however, does contain a significant number of real offense elements. For one thing, the hundreds of overlapping and duplicative statutory provisions that make up the federal criminal law forced the Commission to write guidelines that are descriptive of generic conduct rather than guidelines that track purely statutory language. For another, the guidelines take account of a number of important, commonly occurring real offense elements such as role in the offense, the presence of a gun, or the amount of money actually taken, through alternative base offense levels, specific offense characteristics, cross references, and adjustments.</p><p>The Commission recognized that a charge offense system has drawbacks of its own. One of the most important is the potential it affords prosecutors to influence sentences by increasing or decreasing the number of counts in an indictment. Of course, the defendant’s actual conduct (that which the prosecutor can prove in court) imposes a natural limit upon the prosecutor’s ability to increase a defendant’s sentence. Moreover, the Commission has written its rules for the treatment of multicount convictions with an eye toward eliminating unfair treatment that might flow from count manipulation. For example, the guidelines treat a three-count indictment, each count of which charges sale of 100 grams of heroin or theft of $10,000, the same as a single-count indictment charging sale of 300 grams of heroin or theft of $30,000. Furthermore, a sentencing court may control any inappropriate manipulation of the indictment through use of its departure power. Finally, the Commission will closely monitor charging and plea agreement practices and will make appropriate adjustments should they become necessary.</p><p><strong>(b) Departures.</strong></p><p>The sentencing statute permits a court to depart from a guideline-specified sentence only when it finds “an aggravating or mitigating circumstance of a kind, or to a degree, not adequately taken into consideration by the Sentencing Commission in formulating the guidelines that should result in a sentence different from that described.” 18 U.S.C. § 3553(b). The Commission intends the sentencing courts to treat each guideline as carving out a “heartland,” a set of typical cases embodying the conduct that each guideline describes. When a court finds an atypical case, one to which a particular guideline linguistically applies but where conduct significantly differs from the norm, the court may consider whether a departure is warranted. Section 5H1.10 (Race, Sex, National Origin, Creed, Religion, and Socio-Economic Status), §5H1.12 (Lack of Guidance as a Youth and Similar Circumstances), the third sentence of §5H1.4 (Physical Condition, Including Drug or Alcohol Dependence or Abuse), the last sentence of §5K2.12 (Coercion and Duress), and §5K2.19 (Post-Sentencing Rehabilitative Efforts) list several factors that the court cannot take into account as grounds for departure. With those specific exceptions, however, the Commission does not intend to limit the kinds of factors, whether or not mentioned anywhere else in the guidelines, that could constitute grounds for departure in an unusual case.</p><p>The Commission has adopted this departure policy for two reasons. First, it is difficult to prescribe a single set of guidelines that encompasses the vast range of human conduct potentially relevant to a sentencing decision. The Commission also recognizes that the initial set of guidelines need not do so. The Commission is a permanent body, empowered by law to write and rewrite guidelines, with progressive changes, over many years. By monitoring when courts depart from the guidelines and by analyzing their stated reasons for doing so and court decisions with references thereto, the Commission, over time, will be able to refine the guidelines to specify more precisely when departures should and should not be permitted.</p><p>Second, the Commission believes that despite the courts’ legal freedom to depart from the guidelines, they will not do so very often. This is because the guidelines, offense by offense, seek to take account of those factors that the Commission’s data indicate made a significant difference in pre-guidelines sentencing practice. Thus, for example, where the presence of physical injury made an important difference in pre-guidelines sentencing practice (as in the case of robbery or assault), the guidelines specifically include this factor to enhance the sentence. Where the guidelines do not specify an augmentation or diminution, this is generally because the sentencing data did not permit the Commission to conclude that the factor was empirically important in relation to the particular offense. Of course, an important factor (<em>e.g.</em>, physical injury) may infrequently occur in connection with a particular crime (<em>e.g.</em>, fraud). Such rare occurrences are precisely the type of events that the courts’ departure powers were designed to cover — unusual cases outside the range of the more typical offenses for which the guidelines were designed.</p><p>It is important to note that the guidelines refer to two different kinds of departure. The first involves instances in which the guidelines provide specific guidance for departure by analogy or by other numerical or non-numerical suggestions. The Commission intends such suggestions as policy guidance for the courts. The Commission expects that most departures will reflect the suggestions and that the courts of appeals may prove more likely to find departures “unreasonable” where they fall outside suggested levels.</p><p>A second type of departure will remain unguided. It may rest upon grounds referred to in Chapter Five, Part K (Departures) or on grounds not mentioned in the guidelines. While Chapter Five, Part K lists factors that the Commission believes may constitute grounds for departure, the list is not exhaustive. The Commission recognizes that there may be other grounds for departure that are not mentioned; it also believes there may be cases in which a departure outside suggested levels is warranted. In its view, however, such cases will be highly infrequent.</p><p><strong>(c) Plea Agreements.</strong></p><p>Nearly ninety percent of all federal criminal cases involve guilty pleas and many of these cases involve some form of plea agreement. Some commentators on early Commission guideline drafts urged the Commission not to attempt any major reforms of the plea agreement process on the grounds that any set of guidelines that threatened to change pre-guidelines practice radically also threatened to make the federal system unmanageable. Others argued that guidelines that failed to control and limit plea agreements would leave untouched a “loophole” large enough to undo the good that sentencing guidelines would bring.</p><p>The Commission decided not to make major changes in plea agreement practices in the initial guidelines, but rather to provide guidance by issuing general policy statements concerning the acceptance of plea agreements in Chapter Six, Part B (Plea Agreements). The rules set forth in Fed. R. Crim. P. 11(e) govern the acceptance or rejection of such agreements. The Commission will collect data on the courts’ plea practices and will analyze this information to determine when and why the courts accept or reject plea agreements and whether plea agreement practices are undermining the intent of the Sentencing Reform Act. In light of this information and analysis, the Commission will seek to further regulate the plea agreement process as appropriate. Importantly, if the policy statements relating to plea agreements are followed, circumvention of the Sentencing Reform Act and the guidelines should not occur.</p><p>The Commission expects the guidelines to have a positive, rationalizing impact upon plea agreements for two reasons. First, the guidelines create a clear, definite expectation in respect to the sentence that a court will impose if a trial takes place. In the event a prosecutor and defense attorney explore the possibility of a negotiated plea, they will no longer work in the dark. This fact alone should help to reduce irrationality in respect to actual sentencing outcomes. Second, the guidelines create a norm to which courts will likely refer when they decide whether, under Rule 11(e), to accept or to reject a plea agreement or recommendation.</p><p><strong>(d) Probation and Split Sentences.</strong></p><p>The statute provides that the guidelines are to “reflect the general appropriateness of imposing a sentence other than imprisonment in cases in which the defendant is a first offender who has not been convicted of a crime of violence or an otherwise serious offense . . . .” 28 U.S.C. § 994(j). Under pre-guidelines sentencing practice, courts sentenced to probation an inappropriately high percentage of offenders guilty of certain economic crimes, such as theft, tax evasion, antitrust offenses, insider trading, fraud, and embezzlement, that in the Commission’s view are “serious.”</p><p>The Commission’s solution to this problem has been to write guidelines that classify as serious many offenses for which probation previously was frequently given and provide for at least a short period of imprisonment in such cases. The Commission concluded that the definite prospect of prison, even though the term may be short, will serve as a significant deterrent, particularly when compared with pre-guidelines practice where probation, not prison, was the norm.</p><p>More specifically, the guidelines work as follows in respect to a first offender. For offense levels one through eight, the sentencing court may elect to sentence the offender to probation (with or without confinement conditions) or to a prison term. For offense levels nine and ten, the court may substitute probation for a prison term, but the probation must include confinement conditions (community confinement, intermittent confinement, or home detention). For offense levels eleven and twelve, the court must impose at least one-half the minimum confinement sentence in the form of prison confinement, the remainder to be served on supervised release with a condition of community confinement or home detention. The Commission, of course, has not dealt with the single acts of aberrant behavior that still may justify probation at higher offense levels through departures.*</p><p>*Note: Although the Commission had not addressed “single acts of aberrant behavior” at the time the Introduction to the Guidelines Manual originally was written, it subsequently addressed the issue in Amendment 603, effective November 1, 2000. (<em>See</em> Supplement to Appendix C, amendment 603.)</p><p><strong>(e) Multi-Count Convictions.</strong></p><p>The Commission, like several state sentencing commissions, has found it particularly difficult to develop guidelines for sentencing defendants convicted of multiple violations of law, each of which makes up a separate count in an indictment. The difficulty is that when a defendant engages in conduct that causes several harms, each additional harm, even if it increases the extent to which punishment is warranted, does not necessarily warrant a proportionate increase in punishment. A defendant who assaults others during a fight, for example, may warrant more punishment if he injures ten people than if he injures one, but his conduct does not necessarily warrant ten times the punishment. If it did, many of the simplest offenses, for reasons that are often fortuitous, would lead to sentences of life imprisonment — sentences that neither just deserts nor crime control theories of punishment would justify.</p><p>Several individual guidelines provide special instructions for increasing punishment when the conduct that is the subject of that count involves multiple occurrences or has caused several harms. The guidelines also provide general rules for aggravating punishment in light of multiple harms charged separately in separate counts. These rules may produce occasional anomalies, but normally they will permit an appropriate degree of aggravation of punishment for multiple offenses that are the subjects of separate counts.</p><p>These rules are set out in Chapter Three, Part D (Multiple Counts). They essentially provide: (1) when the conduct involves fungible items (<em>e.g.</em>, separate drug transactions or thefts of money), the amounts are added and the guidelines apply to the total amount; (2) when nonfungible harms are involved, the offense level for the most serious count is increased (according to a diminishing scale) to reflect the existence of other counts of conviction. The guidelines have been written in order to minimize the possibility that an arbitrary casting of a single transaction into several counts will produce a longer sentence. In addition, the sentencing court will have adequate power to prevent such a result through departures.</p><p><strong>(f) Regulatory Offenses.</strong></p><p>Regulatory statutes, though primarily civil in nature, sometimes contain criminal provisions in respect to particularly harmful activity. Such criminal provisions often describe not only substantive offenses, but also more technical, administratively-related offenses such as failure to keep accurate records or to provide requested information. These statutes pose two problems: first, which criminal regulatory provisions should the Commission initially consider, and second, how should it treat technical or administratively-related criminal violations?</p><p>In respect to the first problem, the Commission found that it could not comprehensively treat all regulatory violations in the initial set of guidelines. There are hundreds of such provisions scattered throughout the United States Code. To find all potential violations would involve examination of each individual federal regulation. Because of this practical difficulty, the Commission sought to determine, with the assistance of the Department of Justice and several regulatory agencies, which criminal regulatory offenses were particularly important in light of the need for enforcement of the general regulatory scheme. The Commission addressed these offenses in the initial guidelines.</p><p>In respect to the second problem, the Commission has developed a system for treating technical recordkeeping and reporting offenses that divides them into four categories. First, in the simplest of cases, the offender may have failed to fill out a form intentionally, but without knowledge or intent that substantive harm would likely follow. He might fail, for example, to keep an accurate record of toxic substance transport, but that failure may not lead, nor be likely to lead, to the release or improper handling of any toxic substance. Second, the same failure may be accompanied by a significant likelihood that substantive harm will occur; it may make a release of a toxic substance more likely. Third, the same failure may have led to substantive harm. Fourth, the failure may represent an effort to conceal a substantive harm that has occurred.</p><p>The structure of a typical guideline for a regulatory offense provides a low base offense level (<em>e.g.</em>, 6) aimed at the first type of recordkeeping or reporting offense. Specific offense characteristics designed to reflect substantive harms that do occur in respect to some regulatory offenses, or that are likely to occur, increase the offense level. A specific offense characteristic also provides that a recordkeeping or reporting offense that conceals a substantive offense will have the same offense level as the substantive offense.</p><p><strong>(g) Sentencing Ranges.</strong></p><p>In determining the appropriate sentencing ranges for each offense, the Commission estimated the average sentences served within each category under the pre-guidelines sentencing system. It also examined the sentences specified in federal statutes, in the parole guidelines, and in other relevant, analogous sources. The Commission’s Supplementary Report on the Initial Sentencing Guidelines (1987) contains a comparison between estimates of pre-guidelines sentencing practice and sentences under the guidelines.</p><p>While the Commission has not considered itself bound by pre-guidelines sentencing practice, it has not attempted to develop an entirely new system of sentencing on the basis of theory alone. Guideline sentences, in many instances, will approximate average pre-guidelines practice and adherence to the guidelines will help to eliminate wide disparity. For example, where a high percentage of persons received probation under pre-guidelines practice, a guideline may include one or more specific offense characteristics in an effort to distinguish those types of defendants who received probation from those who received more severe sentences. In some instances, short sentences of incarceration for all offenders in a category have been substituted for a pre-guidelines sentencing practice of very wide variability in which some defendants received probation while others received several years in prison for the same offense. Moreover, inasmuch as those who pleaded guilty under pre-guidelines practice often received lesser sentences, the guidelines permit the court to impose lesser sentences on those defendants who accept responsibility for their misconduct. For defendants who provide substantial assistance to the government in the investigation or prosecution of others, a downward departure may be warranted.</p><p>The Commission has also examined its sentencing ranges in light of their likely impact upon prison population. Specific legislation, such as the Anti-Drug Abuse Act of 1986 and the career offender provisions of the Sentencing Reform Act of 1984 (28 U.S.C. § 994(h)), required the Commission to promulgate guidelines that will lead to substantial prison population increases. These increases will occur irrespective of the guidelines. The guidelines themselves, insofar as they reflect policy decisions made by the Commission (rather than legislated mandatory minimum or career offender sentences), are projected to lead to an increase in prison population that computer models, produced by the Commission and the Bureau of Prisons in 1987, estimated at approximately 10 percent over a period of ten years.</p><p><strong>(h) The Sentencing Table.</strong></p><p>The Commission has established a sentencing table that for technical and practical reasons contains 43 levels. Each level in the table prescribes ranges that overlap with the ranges in the preceding and succeeding levels. By overlapping the ranges, the table should discourage unnecessary litigation. Both prosecution and defense will realize that the difference between one level and another will not necessarily make a difference in the sentence that the court imposes. Thus, little purpose will be served in protracted litigation trying to determine, for example, whether $10,000 or $11,000 was obtained as a result of a fraud. At the same time, the levels work to increase a sentence proportionately. A change of six levels roughly doubles the sentence irrespective of the level at which one starts. The guidelines, in keeping with the statutory requirement that the maximum of any range cannot exceed the minimum by more than the greater of 25 percent or six months (28 U.S.C. § 994(b)(2)), permit courts to exercise the greatest permissible range of sentencing discretion. The table overlaps offense levels meaningfully, works proportionately, and at the same time preserves the maximum degree of allowable discretion for the court within each level.</p><p>Similarly, many of the individual guidelines refer to tables that correlate amounts of money with offense levels. These tables often have many rather than a few levels. Again, the reason is to minimize the likelihood of unnecessary litigation. If a money table were to make only a few distinctions, each distinction would become more important and litigation over which category an offender fell within would become more likely. Where a table has many small monetary distinctions, it minimizes the likelihood of litigation because the precise amount of money involved is of considerably less importance.</p><p><strong>5. A Concluding Note</strong></p><p>The Commission emphasizes that it drafted the initial guidelines with considerable caution. It examined the many hundreds of criminal statutes in the United States Code. It began with those that were the basis for a significant number of prosecutions and sought to place them in a rational order. It developed additional distinctions relevant to the application of these provisions and it applied sentencing ranges to each resulting category. In doing so, it relied upon pre-guidelines sentencing practice as revealed by its own statistical analyses based on summary reports of some 40,000 convictions, a sample of 10,000 augmented presentence reports, the parole guidelines, and policy judgments.</p><p>The Commission recognizes that some will criticize this approach as overly cautious, as representing too little a departure from pre-guidelines sentencing practice. Yet, it will cure wide disparity. The Commission is a permanent body that can amend the guidelines each year. Although the data available to it, like all data, are imperfect, experience with the guidelines will lead to additional information and provide a firm empirical basis for consideration of revisions.</p><p>Finally, the guidelines will apply to more than 90 percent of all felony and Class A misdemeanor cases in the federal courts. Because of time constraints and the nonexistence of statistical information, some offenses that occur infrequently are not considered in the guidelines. Their exclusion does not reflect any judgment regarding their seriousness and they will be addressed as the Commission refines the guidelines over time.</p></blockquote><h3 id='continuing-evolution-and-role-of-the-guidelines'>2. Continuing Evolution and Role of the Guidelines</h3><p>The Sentencing Reform Act of 1984 changed the course of federal sentencing. Among other things, the Act created the United States Sentencing Commission as an independent agency in the Judicial Branch, and directed it to develop guidelines and policy statements for sentencing courts to use when sentencing offenders convicted of federal crimes. Moreover, it empowered the Commission with ongoing responsibilities to monitor the guidelines, submit to Congress appropriate modifications of the guidelines and recommended changes in criminal statutes, and establish education and research programs. The mandate rested on congressional awareness that sentencing is a dynamic field that requires continuing review by an expert body to revise sentencing policies, in light of application experience, as new criminal statutes are enacted, and as more is learned about what motivates and controls criminal behavior.</p><p>This statement finds resonance in a line of Supreme Court cases that, taken together, echo two themes. The first theme is that the guidelines are the product of a deliberative process that seeks to embody the purposes of sentencing set forth in the Sentencing Reform Act, and as such they continue to play an important role in the sentencing court’s determination of an appropriate sentence in a particular case. The Supreme Court alluded to this in <em>Mistretta v. United States</em>, 488 U.S. 361 (1989), which upheld the constitutionality of both the federal sentencing guidelines and the Commission against nondelegation and separation of powers challenges. Therein the Court stated:</p><blockquote><p>Developing proportionate penalties for hundreds of different crimes by a virtually limitless array of offenders is precisely the sort of intricate, labor-intensive task for which delegation to an expert body is especially appropriate. Although Congress has delegated significant discretion to the Commission to draw judgments from its analysis of existing sentencing practice and alternative sentencing models, . . . [w]e have no doubt that in the hands of the Commission “the criteria which Congress has supplied are wholly adequate for carrying out the general policy and purpose” of the Act.</p></blockquote><p><em>Id.</em> at 379 (internal quotation marks and citations omitted).</p><p>The continuing importance of the guidelines in federal sentencing was further acknowledged by the Court in <em>United States v. Booker</em>, 543 U.S. 220 (2005), even as that case rendered the guidelines advisory in nature. In <em>Booker</em>, the Court held that the imposition of an enhanced sentence under the federal sentencing guidelines based on the sentencing judge’s determination of a fact (other than a prior conviction) that was not found by the jury or admitted by the defendant violated the Sixth Amendment. The Court reasoned that an advisory guideline system, while lacking the mandatory features that Congress enacted, retains other features that help to further congressional objectives, including providing certainty and fairness in meeting the purposes of sentencing, avoiding unwarranted sentencing disparities, and maintaining sufficient flexibility to permit individualized sentences when warranted. The Court concluded that an advisory guideline system would “continue to move sentencing in Congress’ preferred direction, helping to avoid excessive sentencing disparities while maintaining flexibility sufficient to individualize sentences where necessary.” <em>Id.</em> at 264–65. An advisory guideline system continues to assure transparency by requiring that sentences be based on articulated reasons stated in open court that are subject to appellate review. An advisory guideline system also continues to promote certainty and predictability in sentencing, thereby enabling the parties to better anticipate the likely sentence based on the individualized facts of the case.</p><p>The continuing importance of the guidelines in the sentencing determination is predicated in large part on the Sentencing Reform Act’s intent that, in promulgating guidelines, the Commission must take into account the purposes of sentencing as set forth in 18 U.S.C. § 3553(a). <em>See</em> 28 U.S.C. §§ 994(f), 991(b)(1). The Supreme Court reinforced this view in <em>Rita v. United States</em>, 551 U.S. 338 (2007), which held that a court of appeals may apply a presumption of reasonableness to a sentence imposed by a district court within a properly calculated guideline range without violating the Sixth Amendment. In <em>Rita</em>, the Court relied heavily on the complementary roles of the Commission and the sentencing court in federal sentencing, stating:</p><blockquote><p>[T]he presumption reflects the nature of the Guidelines-writing task that Congress set for the Commission and the manner in which the Commission carried out that task. In instructing both the <em>sentencing judge</em> and the <em>Commission</em> what to do, Congress referred to the basic sentencing objectives that the statute sets forth in 18 U.S.C. § 3553(a) . . . . The provision also tells the sentencing judge to “impose a sentence sufficient, but not greater than necessary, to comply with” the basic aims of sentencing as set out above. Congressional statutes then tell the <em>Commission</em> to write Guidelines that will carry out these same § 3553(a) objectives.</p></blockquote><p><em>Id.</em> at 347–48 (emphasis in original). The Court concluded that “[t]he upshot is that the sentencing statutes envision both the sentencing judge and the Commission as carrying out the same basic § 3553(a) objectives, the one, at retail, the other at wholesale[,]” <em>id.</em> at 348, and that the Commission’s process for promulgating guidelines results in “a set of Guidelines that seek to embody the § 3553(a) considerations, both in principle and in practice.” <em>Id.</em> at 350.</p><p>Consequently, district courts are required to properly calculate and consider the guidelines when sentencing, even in an advisory guideline system. <em>See</em> 18 U.S.C. § 3553(a)(4), (a)(5); <em>Booker</em>, 543 U.S. at 264 (“The district courts, while not bound to apply the Guidelines, must . . . take them into account when sentencing.”); <em>Rita</em>, 551 U.S. at 351 (stating that a district court should begin all sentencing proceedings by correctly calculating the applicable Guidelines range); <em>Gall v. United States</em>, 552 U.S. 38, 49 (2007) (“As a matter of administration and to secure nationwide consistency, the Guidelines should be the starting point and the initial benchmark.”). The district court, in determining the appropriate sentence in a particular case, therefore, must consider the properly calculated guideline range, the grounds for departure provided in the policy statements, and then the factors under 18 U.S.C. § 3553(a). <em>See Rita</em>, 551 U.S. at 351. The appellate court engages in a two-step process upon review. The appellate court “first ensure[s] that the district court committed no significant procedural error, such as failing to calculate (or improperly calculating) the Guidelines range . . . [and] then consider[s] the substantive reasonableness of the sentence imposed under an abuse-of-discretion standard[,] . . . tak[ing] into account the totality of the circumstances, including the extent of any variance from the Guidelines range.” <em>Gall</em>, 552 U.S. at 51.</p><p>The second and related theme resonant in this line of Supreme Court cases is that, as contemplated by the Sentencing Reform Act, the guidelines are evolutionary in nature. They are the product of the Commission’s fulfillment of its statutory duties to monitor federal sentencing law and practices, to seek public input on the operation of the guidelines, and to revise the guidelines accordingly. As the Court acknowledged in <em>Rita</em>:</p><blockquote><p>The Commission’s work is ongoing. The statutes and the Guidelines themselves foresee continuous evolution helped by the sentencing courts and courts of appeals in that process. The sentencing courts, applying the Guidelines in individual cases may depart (either pursuant to the Guidelines or, since <em>Booker</em>, by imposing a non-Guidelines sentence). The judges will set forth their reasons. The Courts of Appeals will determine the reasonableness of the resulting sentence. The Commission will collect and examine the results. In doing so, it may obtain advice from prosecutors, defenders, law enforcement groups, civil liberties associations, experts in penology, and others. And it can revise the Guidelines accordingly.</p></blockquote><p><em>Rita</em>, 551 U.S. at 350; <em>see also Booker</em>, 543 U.S. at 264 (“[T]he Sentencing Commission remains in place, writing Guidelines, collecting information about actual district court sentencing decisions, undertaking research, and revising the Guidelines accordingly.”); <em>Gall</em>, 552 U.S. at 46 (“[E]ven though the Guidelines are advisory rather than mandatory, they are, as we pointed out in <em>Rita</em>, the product of careful study based on extensive empirical evidence derived from the review of thousands of individual sentencing decisions.”).</p><p>Provisions of the Sentencing Reform Act promote and facilitate this evolutionary process. For example, pursuant to 28 U.S.C. § 994(x), the Commission publishes guideline amendment proposals in the <em>Federal Register</em> and conducts hearings to solicit input on those proposals from experts and other members of the public. Pursuant to 28 U.S.C. § 994(o), the Commission periodically reviews and revises the guidelines in consideration of comments it receives from members of the federal criminal justice system, including the courts, probation officers, the Department of Justice, the Bureau of Prisons, defense attorneys and the federal public defenders, and in consideration of data it receives from sentencing courts and other sources. Statutory mechanisms such as these bolster the Commission’s ability to take into account fully the purposes of sentencing set forth in 18 U.S.C. § 3553(a)(2) in its promulgation of the guidelines.</p><p>Congress retains authority to require certain sentencing practices and may exercise its authority through specific directives to the Commission with respect to the guidelines. As the Supreme Court noted in <em>Kimbrough v. United States</em>, 552 U.S. 85 (2007), “Congress has shown that it knows how to direct sentencing practices in express terms. For example, Congress has specifically required the Sentencing Commission to set Guideline sentences for serious recidivist offenders ‘at or near’ the statutory maximum.” <em>Id.</em> at 103; 28 U.S.C. § 994(h).</p><p>As envisioned by Congress, implemented by the Commission, and reaffirmed by the Supreme Court, the guidelines are the product of a deliberative and dynamic process that seeks to embody within federal sentencing policy the purposes of sentencing set forth in the Sentencing Reform Act. As such, the guidelines continue to be a key component of federal sentencing and to play an important role in the sentencing court’s determination of an appropriate sentence in any particular case.</p><h3 id='section'></h3><h3 id='authority'>3. Authority</h3>"},{"id":"B","type":"part","chapter":"1","title":"GENERAL APPLICATION PRINCIPLES"},{"id":"A","type":"part","chapter":"2","title":"OFFENSES AGAINST THE PERSON"},{"id":"B","type":"part","chapter":"2","title":"BASIC ECONOMIC OFFENSES"},{"id":"C","type":"part","chapter":"2","title":"OFFENSES INVOLVING PUBLIC OFFICIALS AND VIOLATION OF FEDERAL ELECTION CAMPAIGN LAWS","content":"<table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987. Amended effective January 25, 2003 (amendment 648). Introductory Commentary to Part C, effective November 1, 1987, was deleted effective January 25, 2003 (amendment 648), and November 1, 2003 (amendment 656).</td></tr></tbody></table>"},{"id":"D","type":"part","chapter":"2","title":"OFFENSES INVOLVING DRUGS AND NARCO-TERRORISM","content":"<table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987. Amended effective November 1, 2007 (amendment 711).</td></tr></tbody></table>"},{"id":"E","type":"part","chapter":"2","title":"OFFENSES INVOLVING CRIMINAL ENTERPRISES AND RACKETEERING"},{"id":"F","type":"part","chapter":"2","title":"[DELETED]","content":"<table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>The heading to Part F ― Offenses Involving Fraud or Deceit, effective November 1, 1987, was deleted due to the deletion of §§2F1.1 and 2F1.2 effective November 1, 2001 (amendment 617).</td></tr></tbody></table>"},{"id":"G","type":"part","chapter":"2","title":"OFFENSES INVOLVING COMMERCIAL SEX ACTS, SEXUAL EXPLOITATION OF MINORS, AND OBSCENITY","content":"<table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987. Amended effective November 1, 2002 (amendment 641).</td></tr></tbody></table>"},{"id":"H","type":"part","chapter":"2","title":"OFFENSES INVOLVING INDIVIDUAL RIGHTS"},{"id":"I","type":"part","chapter":"2","title":"[NOT USED]"},{"id":"J","type":"part","chapter":"2","title":"OFFENSES INVOLVING THE ADMINISTRATION OF JUSTICE"},{"id":"K","type":"part","chapter":"2","title":"OFFENSES INVOLVING PUBLIC SAFETY"},{"id":"L","type":"part","chapter":"2","title":"OFFENSES INVOLVING IMMIGRATION, NATURALIZATION, AND PASSPORTS"},{"id":"M","type":"part","chapter":"2","title":"OFFENSES INVOLVING NATIONAL DEFENSE AND WEAPONS OF MASS DESTRUCTION","content":"<table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987. Amended effective November 1, 2001 (amendment 633).</td></tr></tbody></table>"},{"id":"N","type":"part","chapter":"2","title":"OFFENSES INVOLVING FOOD, DRUGS, AGRICULTURAL PRODUCTS, AND ODOMETER LAWS","content":"<table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987. Amended effective November 1, 2009 (amendment 733).</td></tr></tbody></table>"},{"id":"O","type":"part","chapter":"2","title":"[NOT USED]"},{"id":"P","type":"part","chapter":"2","title":"OFFENSES INVOLVING PRISONS AND CORRECTIONAL FACILITIES"},{"id":"Q","type":"part","chapter":"2","title":"OFFENSES INVOLVING THE ENVIRONMENT"},{"id":"R","type":"part","chapter":"2","title":"ANTITRUST OFFENSES"},{"id":"S","type":"part","chapter":"2","part":"S","title":"MONEY LAUNDERING AND MONETARY TRANSACTION REPORTING","content":"<table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Introductory Commentary to this Part, effective November 1, 1987, was deleted effective November 1, 1990 (amendment 342).</td></tr></tbody></table>"},{"id":"T","type":"part","chapter":"2","title":"OFFENSES INVOLVING TAXATION"},{"id":"U","type":"part","chapter":"2","title":"[NOT USED]"},{"id":"V","type":"part","chapter":"2","title":"[NOT USED]"},{"id":"W","type":"part","chapter":"2","title":"[NOT USED]"},{"id":"X","type":"part","chapter":"2","title":"OTHER OFFENSES"},{"id":"Y","type":"part","chapter":"2","title":"[NOT USED]"},{"id":"Z","type":"part","chapter":"2","title":"[NOT USED]"},{"id":"A","type":"part","chapter":"3","title":"VICTIM-RELATED ADJUSTMENTS","content":"<h4 id='introductory-commentary-9' class='Commentary'><strong>Introductory Commentary</strong></h4><p>The following adjustments are included in this Part because they may apply to a wide variety of offenses.</p><table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987. Amended effective November 1, 1990 (amendment 344).</td></tr></tbody></table>"},{"id":"B","type":"part","chapter":"3","title":"ROLE IN THE OFFENSE","content":"<h4 id='introductory-commentary-10' class='Commentary'><strong>Introductory Commentary</strong></h4><p>This Part provides adjustments to the offense level based upon the role the defendant played in committing the offense. The determination of a defendant’s role in the offense is to be made on the basis of all conduct within the scope of §1B1.3 (Relevant Conduct), <em>i.e.</em>, all conduct included under §1B1.3(a)(1)–(4), and not solely on the basis of elements and acts cited in the count of conviction.</p><p>When an offense is committed by more than one participant, §3B1.1 or §3B1.2 (or neither) may apply. Section 3B1.3 may apply to offenses committed by any number of participants.</p><table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987. Amended effective November 1, 1990 (amendment 345); November 1, 1992 (amendment 456).</td></tr></tbody></table>"},{"id":"C","type":"part","chapter":"3","title":"OBSTRUCTION AND RELATED ADJUSTMENTS","content":"<table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987. Amended effective November 1, 2006 (amendment 684).</td></tr></tbody></table>"},{"id":"D","type":"part","chapter":"3","title":"MULTIPLE COUNTS","content":"<h4 id='introductory-commentary-11' class='Commentary'><strong>Introductory Commentary</strong></h4><p>This Part provides rules for determining a single offense level that encompasses all the counts of which the defendant is convicted. These rules apply to multiple counts of conviction (A) contained in the same indictment or information; or (B) contained in different indictments or informations for which sentences are to be imposed at the same time or in a consolidated proceeding. The single, “combined” offense level that results from applying these rules is used, after adjustment pursuant to the guidelines in subsequent parts, to determine the sentence. These rules have been designed primarily with the more commonly prosecuted federal offenses in mind.</p><p>The rules in this Part seek to provide incremental punishment for significant additional criminal conduct. The most serious offense is used as a starting point. The other counts determine how much to increase the offense level. The amount of the additional punishment declines as the number of additional offenses increases.</p><p>Some offenses that may be charged in multiple-count indictments are so closely intertwined with other offenses that conviction for them ordinarily would not warrant increasing the guideline range. For example, embezzling money from a bank and falsifying the related records, although legally distinct offenses, represent essentially the same type of wrongful conduct with the same ultimate harm, so that it would be more appropriate to treat them as a single offense for purposes of sentencing. Other offenses, such as an assault causing bodily injury to a teller during a bank robbery, are so closely related to the more serious offense that it would be appropriate to treat them as part of the more serious offense, leaving the sentence enhancement to result from application of a specific offense characteristic.</p><p>In order to limit the significance of the formal charging decision and to prevent multiple punishment for substantially identical offense conduct, this Part provides rules for grouping offenses together. Convictions on multiple counts do not result in a sentence enhancement unless they represent additional conduct that is not otherwise accounted for by the guidelines. In essence, counts that are grouped together are treated as constituting a single offense for purposes of the guidelines.</p><p>Some offense guidelines, such as those for theft, fraud and drug offenses, contain provisions that deal with repetitive or ongoing behavior. Other guidelines, such as those for assault and robbery, are oriented more toward single episodes of criminal behavior. Accordingly, different rules are required for dealing with multiple-count convictions involving these two different general classes of offenses. More complex cases involving different types of offenses may require application of one rule to some of the counts and another rule to other counts.</p><p>Some offenses, <em>e.g.</em>, racketeering and conspiracy, may be “composite” in that they involve a pattern of conduct or scheme involving multiple underlying offenses. The rules in this Part are to be used to determine the offense level for such composite offenses from the offense level for the underlying offenses.</p><p>Essentially, the rules in this Part can be summarized as follows: (1) If the offense guidelines in Chapter Two base the offense level primarily on the amount of money or quantity of substance involved (<em>e.g.</em>, theft, fraud, drug trafficking, firearms dealing), or otherwise contain provisions dealing with repetitive or ongoing misconduct <em>(e.g.</em>, many environmental offenses), add the numerical quantities and apply the pertinent offense guideline, including any specific offense characteristics for the conduct taken as a whole. (2) When offenses are closely interrelated, group them together for purposes of the multiple-count rules, and use only the offense level for the most serious offense in that group. (3) As to other offenses (<em>e.g.</em>, independent instances of assault or robbery), start with the offense level for the most serious count and use the number and severity of additional counts to determine the amount by which to increase that offense level.</p><table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987. Amended effective November 1, 1989 (amendment 121); November 1, 2007 (amendment 707).</td></tr></tbody></table>"},{"id":"E","type":"part","chapter":"3","title":"ACCEPTANCE OF RESPONSIBILITY"},{"id":"A","type":"part","chapter":"4","title":"CRIMINAL HISTORY","content":"<h4 id='introductory-commentary-12' class='Commentary'><strong>Introductory Commentary</strong></h4><p>The Comprehensive Crime Control Act sets forth four purposes of sentencing. (<em>See</em> 18 U.S.C. § 3553(a)(2).) A defendant’s record of past criminal conduct is directly relevant to those purposes. A defendant with a record of prior criminal behavior is more culpable than a first offender and thus deserving of greater punishment. General deterrence of criminal conduct dictates that a clear message be sent to society that repeated criminal behavior will aggravate the need for punishment with each recurrence. To protect the public from further crimes of the particular defendant, the likelihood of recidivism and future criminal behavior must be considered. Repeated criminal behavior is an indicator of a limited likelihood of successful rehabilitation.</p><p>The specific factors included in §4A1.1 and §4A1.3 are consistent with the extant empirical research assessing correlates of recidivism and patterns of career criminal behavior. While empirical research has shown that other factors are correlated highly with the likelihood of recidivism, <em>e.g.</em>, age and drug abuse, for policy reasons they were not included here at this time. The Commission has made no definitive judgment as to the reliability of the existing data. However, the Commission will review additional data insofar as they become available in the future.</p><table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987.</td></tr></tbody></table>"},{"id":"B","type":"part","chapter":"4","title":"CAREER OFFENDERS AND CRIMINAL LIVELIHOOD"},{"id":"A","type":"part","chapter":"5","title":"SENTENCING TABLE","content":" <h2>Sentencing Table</h2><p>The Sentencing Table used to determine the guideline range follows:</p><p><img src='/Sentencing_Table.svg' alt='Sentencing Table' /></p><h4 id='commentary-to-sentencing-table' class='Commentary'><strong>Commentary to Sentencing Table</strong></h4><p><strong>Application Notes:</strong></p><p>1. The Offense Level (1–43) forms the vertical axis of the SentencingTable. The Criminal History Category (I–VI) forms the horizontal axis ofthe Table. The intersection of the Offense Level and Criminal HistoryCategory displays the Guideline Range in months of imprisonment. “<em><strong>Life</strong></em>” means life imprisonment. For example, the guideline rangeapplicable to a defendant with an Offense Level of 15 and a CriminalHistory Category of III is 24–30 months of imprisonment.</p><p>2. In rare cases, a total offense level of less than 1 or more than 43may result from application of the guidelines. A total offense level ofless than 1 is to be treated as an offense level of 1. An offense levelof more than 43 is to be treated as an offense level of 43.</p><p>3. The Criminal History Category is determined by the total criminalhistory points from Chapter Four, Part A, except as provided in §§4B1.1(Career Offender) and 4B1.4 (Armed Career Criminal). The total criminalhistory points associated with each Criminal History Category are shownunder each Criminal History Category in the Sentencing Table.</p><table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987. Amended effective November 1, 1989(amendment 270); November 1, 1991 (amendment 418); November 1,1992 (amendment 462); November 1, 2010 (amendment 738).</td></tr></tbody></table>"},{"id":"B","type":"part","chapter":"5","title":"PROBATION","content":"<h4 id='introductory-commentary-14' class='Commentary'><strong>Introductory Commentary</strong></h4><p>The Comprehensive Crime Control Act of 1984 makes probation a sentence in and of itself. 18 U.S.C. § 3561. Probation may be used as an alternative to incarceration, provided that the terms and conditions of probation can be fashioned so as to meet fully the statutory purposes of sentencing, including promoting respect for law, providing just punishment for the offense, achieving general deterrence, and protecting the public from further crimes by the defendant.</p><table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987.</td></tr></tbody></table>"},{"id":"C","type":"part","chapter":"5","title":"IMPRISONMENT"},{"id":"D","type":"part","chapter":"5","title":"SUPERVISED RELEASE"},{"id":"E","type":"part","chapter":"5","title":"RESTITUTION, FINES, ASSESSMENTS, FORFEITURES"},{"id":"F","type":"part","chapter":"5","title":"SENTENCING OPTIONS"},{"id":"G","type":"part","chapter":"5","title":"IMPLEMENTING THE TOTAL SENTENCE OF IMPRISONMENT"},{"id":"H","type":"part","chapter":"5","title":"SPECIFIC OFFENDER CHARACTERISTICS","content":"<h4 id='introductory-commentary-15' class='Commentary'><strong>Introductory Commentary</strong></h4><p>This Part addresses the relevance of certain specific offender characteristics in sentencing. The Sentencing Reform Act (the “Act”) contains several provisions regarding specific offender characteristics:</p><blockquote><p><em>First</em>, the Act directs the Commission to ensure that the guidelines and policy statements “are entirely neutral” as to five characteristics – race, sex, national origin, creed, and socioeconomic status. <em>See</em> 28 U.S.C. § 994(d).</p><p><em>Second</em>, the Act directs the Commission to consider whether eleven specific offender characteristics, “among others”, have any relevance to the nature, extent, place of service, or other aspects of an appropriate sentence, and to take them into account in the guidelines and policy statements only to the extent that they do have relevance. <em>See</em> 28 U.S.C. § 994(d).</p><p><em>Third</em>, the Act directs the Commission to ensure that the guidelines and policy statements, in recommending a term of imprisonment or length of a term of imprisonment, reflect the “general inappropriateness” of considering five of those characteristics – education; vocational skills; employment record; family ties and responsibilities; and community ties. <em>See</em> 28 U.S.C. § 994(e).</p><p><em>Fourth</em>, the Act also directs the sentencing court, in determining the particular sentence to be imposed, to consider, among other factors, “the history and characteristics of the defendant”. <em>See</em> 18 U.S.C. § 3553(a)(1).</p></blockquote><p>Specific offender characteristics are taken into account in the guidelines in several ways. One important specific offender characteristic is the defendant’s criminal history, <em>see</em> 28 U.S.C. § 994(d)(10), which is taken into account in the guidelines in Chapter Four (Criminal History and Criminal Livelihood). <em>See</em> §5H1.8 (Criminal History). Another specific offender characteristic in the guidelines is the degree of dependence upon criminal history for a livelihood, <em>see</em> 28 U.S.C. § 994(d)(11), which is taken into account in Chapter Four, Part B (Career Offenders and Criminal Livelihood). <em>See</em> §5H1.9 (Dependence upon Criminal Activity for a Livelihood). Other specific offender characteristics are accounted for elsewhere in this manual. <em>See, e.g.</em>, §§2C1.1(a)(1) and 2C1.2(a)(1) (providing alternative base offense levels if the defendant was a public official); 3B1.3 (Abuse of Position of Trust or Use of Special Skill); and 3E1.1 (Acceptance of Responsibility).</p><p>The Supreme Court has emphasized that the advisory guideline system should “continue to move sentencing in Congress’ preferred direction, helping to avoid excessive sentencing disparities while maintaining flexibility sufficient to individualize sentences where necessary.” <em>See United States v. Booker</em>, 543 U.S. 220, 264–65 (2005). Although the court must consider “the history and characteristics of the defendant” among other factors, <em>see</em> 18 U.S.C. § 3553(a), in order to avoid unwarranted sentencing disparities the court should not give them excessive weight. Generally, the most appropriate use of specific offender characteristics is to consider them not as a reason for a sentence outside the applicable guideline range but for other reasons, such as in determining the sentence within the applicable guideline range, the type of sentence (<em>e.g.</em>, probation or imprisonment) within the sentencing options available for the applicable Zone on the Sentencing Table, and various other aspects of an appropriate sentence. To avoid unwarranted sentencing disparities among defendants with similar records who have been found guilty of similar conduct, <em>see</em> 18 U.S.C. § 3553(a)(6), 28 U.S.C. § 991(b)(1)(B), the guideline range, which reflects the defendant’s criminal conduct and the defendant’s criminal history, should continue to be “the starting point and the initial benchmark.” <em>Gall v. United States</em>, 552 U.S. 38, 49 (2007).</p><p>Accordingly, the purpose of this Part is to provide sentencing courts with a framework for addressing specific offender characteristics in a reasonably consistent manner. Using such a framework in a uniform manner will help “secure nationwide consistency,” <em>see Gall v. United States</em>, 552 U.S. 38, 49 (2007), “avoid unwarranted sentencing disparities,” <em>see</em> 28 U.S.C. § 991(b)(1)(B), 18 U.S.C. § 3553(a)(6), “provide certainty and fairness,” <em>see</em> 28 U.S.C. § 991(b)(1)(B), and “promote respect for the law,” <em>see</em> 18 U.S.C. § 3553(a)(2)(A).</p><p>This Part allocates specific offender characteristics into three general categories.</p><p>In the first category are specific offender characteristics the consideration of which Congress has prohibited (<em>e.g.</em>, §5H1.10 (Race, Sex, National Origin, Creed, Religion, and Socio-Economic Status)) or that the Commission has determined should be prohibited.</p><p>In the second category are specific offender characteristics that Congress directed the Commission to take into account in the guidelines only to the extent that they have relevance to sentencing. <em>See</em> 28 U.S.C. § 994(d). For some of these, the policy statements indicate that these characteristics may be relevant in determining whether a sentence outside the applicable guideline range is warranted (<em>e.g.</em>, age; mental and emotional condition; physical condition). These characteristics may warrant a sentence outside the applicable guideline range if the characteristic, individually or in combination with other such characteristics, is present to an unusual degree and distinguishes the case from the typical cases covered by the guidelines. These specific offender characteristics also may be considered for other reasons, such as in determining the sentence within the applicable guideline range, the type of sentence (<em>e.g.</em>, probation or imprisonment) within the sentencing options available for the applicable Zone on the Sentencing Table, and various other aspects of an appropriate sentence.</p><p>In the third category are specific offender characteristics that Congress directed the Commission to ensure are reflected in the guidelines and policy statements as generally inappropriate in recommending a term of imprisonment or length of a term of imprisonment. <em>See </em>28 U.S.C. § 994(e). The policy statements indicate that these characteristics are not ordinarily relevant to the determination of whether a sentence should be outside the applicable guideline range. Unless expressly stated, this does not mean that the Commission views such circumstances as necessarily inappropriate to the determination of the sentence within the applicable guideline range, the type of sentence (<em>e.g.</em>, probation or imprisonment) within the sentencing options available for the applicable Zone on the Sentencing Table, or various other aspects of an appropriate sentence (<em>e.g.</em>, the appropriate conditions of probation or supervised release). Furthermore, although these circumstances are not ordinarily relevant to the determination of whether a sentence should be outside the applicable guideline range, they may be relevant to this determination in exceptional cases. They also may be relevant if a combination of such circumstances makes the case an exceptional one, but only if each such circumstance is identified as an affirmative ground for departure and is present in the case to a substantial degree. <em>See</em> §5K2.0 (Grounds for Departure).</p><p>As with the other provisions in this manual, these policy statements “are evolutionary in nature”. <em>See</em> Chapter One, Part A, Subpart 2 (Continuing Evolution and Role of the Guidelines); 28 U.S.C. § 994(o). The Commission expects, and the Sentencing Reform Act contemplates, that continuing research, experience, and analysis will result in modifications and revisions.</p><p>The nature, extent, and significance of specific offender characteristics can involve a range of considerations. The Commission will continue to provide information to the courts on the relevance of specific offender characteristics in sentencing, as the Sentencing Reform Act contemplates. <em>See, e.g.</em>, 28 U.S.C. § 995(a)(12)(A) (the Commission serves as a “clearinghouse and information center” on federal sentencing). Among other things, this may include information on the use of specific offender characteristics, individually and in combination, in determining the sentence to be imposed (including, where available, information on rates of use, criteria for use, and reasons for use); the relationship, if any, between specific offender characteristics and (A) the “forbidden factors” specified in 28 U.S.C. § 994(d) and (B) the “discouraged factors” specified in 28 U.S.C. § 994(e); and the relationship, if any, between specific offender characteristics and the statutory purposes of sentencing.</p><table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987. Amended effective November 1, 1990 (amendment 357); November 1, 1991 (amendment 386); November 1, 1994 (amendment 508); October 27, 2003 (amendment 651); November 1, 2010 (amendment 739).</td></tr></tbody></table>"},{"id":"I","type":"part","chapter":"5","title":"[NOT USED]"},{"id":"J","type":"part","chapter":"5","title":"RELIEF FROM DISABILITY","content":"<table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987. Amended effective June 15, 1988 (amendment 55).</td></tr></tbody></table>"},{"id":"K","type":"part","chapter":"5","title":"DEPARTURES"},{"id":"A","type":"part","chapter":"6","title":"SENTENCING PROCEDURES","content":"<h4 id='introductory-commentary-16' class='Commentary'><strong>Introductory Commentary</strong></h4><p>This Part addresses sentencing procedures that are applicable in all cases, including those in which guilty or <em>nolo contendere</em> pleas are entered with or without a plea agreement between the parties, and convictions based upon judicial findings or verdicts. It sets forth the procedures for establishing the facts upon which the sentence will be based. Reliable fact-finding is essential to procedural due process and to the accuracy and uniformity of sentencing.</p><table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987.</td></tr></tbody></table>"},{"id":"B","type":"part","chapter":"6","title":"PLEA AGREEMENTS","content":"<h4 id='introductory-commentary-17' class='Commentary'><strong>Introductory Commentary</strong></h4><p>Policy statements governing the acceptance of plea agreements under Rule 11(c), Fed. R. Crim. P., are intended to ensure that plea negotiation practices: (1) promote the statutory purposes of sentencing prescribed in 18 U.S.C. § 3553(a); and (2) do not perpetuate unwarranted sentencing disparity.</p><p>These policy statements make clear that sentencing is a judicial function and that the appropriate sentence in a guilty plea case is to be determined by the judge. The policy statements also ensure that the basis for any judicial decision to depart from the guidelines will be explained on the record.</p><table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1987. Amended effective November 1, 2004 (amendment 674).</td></tr></tbody></table>"},{"id":"A","type":"part","chapter":"7","title":"INTRODUCTION TO CHAPTER SEVEN","content":"<p><strong>1. Authority</strong></p><p>Under 28 U.S.C. § 994(a)(3), the Sentencing Commission is required to issue guidelines or policy statements applicable to the revocation of probation and supervised release. At this time, the Commission has chosen to promulgate policy statements only. These policy statements will provide guidance while allowing for the identification of any substantive or procedural issues that require further review. The Commission views these policy statements as evolutionary and will review relevant data and materials concerning revocation determinations under these policy statements. Revocation guidelines will be issued after federal judges, probation officers, practitioners, and others have the opportunity to evaluate and comment on these policy statements.</p><p><strong>2. Background</strong></p><blockquote><p><strong>(a) Probation.</strong></p></blockquote><p>Prior to the implementation of the federal sentencing guidelines, a court could stay the imposition or execution of sentence and place a defendant on probation. When a court found that a defendant violated a condition of probation, the court could continue probation, with or without extending the term or modifying the conditions, or revoke probation and either impose the term of imprisonment previously stayed, or, where no term of imprisonment had originally been imposed, impose any term of imprisonment that was available at the initial sentencing.</p><p>The statutory authority to “suspend” the imposition or execution of sentence in order to impose a term of probation was abolished upon implementation of the sentencing guidelines. Instead, the Sentencing Reform Act recognized probation as a sentence in itself. 18 U.S.C. § 3561. Under current law, if the court finds that a defendant violated a condition of probation, the court may continue probation, with or without extending the term or modifying the conditions, or revoke probation and impose any other sentence that initially could have been imposed. 18 U.S.C. § 3565. For certain violations, revocation is required by statute.</p><blockquote><p><strong>(b) Supervised Release.</strong></p></blockquote><p>Supervised release, a new form of post-imprisonment supervision created by the Sentencing Reform Act, accompanied implementation of the guidelines. A term of supervised release may be imposed by the court as a part of the sentence of imprisonment at the time of initial sentencing. 18 U.S.C. § 3583(a). Unlike parole, a term of supervised release does not replace a portion of the sentence of imprisonment, but rather is an order of supervision in addition to any term of imprisonment imposed by the court. Accordingly, supervised release is more analogous to the additional “special parole term” previously authorized for certain drug offenses.</p><p>The conditions of supervised release authorized by statute are the same as those for a sentence of probation, except for intermittent confinement. (Intermittent confinement is available for a sentence of probation, but is available as a condition of supervised release only for a violation of a condition of supervised release.) When the court finds that the defendant violated a condition of supervised release, it may continue the defendant on supervised release, with or without extending the term or modifying the conditions, or revoke supervised release and impose a term of imprisonment. The periods of imprisonment authorized by statute for a violation of the conditions of supervised release generally are more limited, however, than those available for a violation of the conditions of probation. 18 U.S.C. § 3583(e)(3).</p><p><strong>3. Resolution of Major Issues</strong></p><blockquote><p><strong>(a) Guidelines versus Policy Statements.</strong></p></blockquote><p>At the outset, the Commission faced a choice between promulgating guidelines or issuing advisory policy statements for the revocation of probation and supervised release. After considered debate and input from judges, probation officers, and prosecuting and defense attorneys, the Commission decided, for a variety of reasons, initially to issue policy statements. Not only was the policy statement option expressly authorized by statute, but this approach provided greater flexibility to both the Commission and the courts. Unlike guidelines, policy statements are not subject to the May 1 statutory deadline for submission to Congress, and the Commission believed that it would benefit from the additional time to consider complex issues relating to revocation guidelines provided by the policy statement option.</p><p>Moreover, the Commission anticipates that, because of its greater flexibility, the policy statement option will provide better opportunities for evaluation by the courts and the Commission. This flexibility is important, given that supervised release as a method of post-incarceration supervision and transformation of probation from a suspension of sentence to a sentence in itself represent recent changes in federal sentencing practices. After an adequate period of evaluation, the Commission intends to promulgate revocation guidelines.</p><blockquote><p><strong>(b) Choice Between Theories.</strong></p></blockquote><p>The Commission debated two different approaches to sanctioning violations of probation and supervised release.</p><p>The first option considered a violation resulting from a defendant’s failure to follow the court-imposed conditions of probation or supervised release as a “breach of trust.” While the nature of the conduct leading to the revocation would be considered in measuring the extent of the breach of trust, imposition of an appropriate punishment for any new criminal conduct would not be the primary goal of a revocation sentence. Instead, the sentence imposed upon revocation would be intended to sanction the violator for failing to abide by the conditions of the court-ordered supervision, leaving the punishment for any new criminal conduct to the court responsible for imposing the sentence for that offense.</p><p>The second option considered by the Commission sought to sanction violators for the particular conduct triggering the revocation as if that conduct were being sentenced as new federal criminal conduct. Under this approach, offense guidelines in Chapters Two and Three of the Guidelines Manual would be applied to any criminal conduct that formed the basis of the violation, after which the criminal history in Chapter Four of the Guidelines Manual would be recalculated to determine the appropriate revocation sentence. This option would also address a violation not constituting a criminal offense.</p><p>After lengthy consideration, the Commission adopted an approach that is consistent with the theory of the first option; <em>i.e.</em>, at revocation the court should sanction primarily the defendant’s breach of trust, while taking into account, to a limited degree, the seriousness of the underlying violation and the criminal history of the violator.</p><p>The Commission adopted this approach for a variety of reasons. First, although the Commission found desirable several aspects of the second option that provided for a detailed revocation guideline system similar to that applied at the initial sentencing, extensive testing proved it to be impractical. In particular, with regard to new criminal conduct that constituted a violation of state or local law, working groups expert in the functioning of federal criminal law noted that it would be difficult in many instances for the court or the parties to obtain the information necessary to apply properly the guidelines to this new conduct. The potential unavailability of information and witnesses necessary for a determination of specific offense characteristics or other guideline adjustments could create questions about the accuracy of factual findings concerning the existence of those factors.</p><p>In addition, the Commission rejected the second option because that option was inconsistent with its views that the court with jurisdiction over the criminal conduct leading to revocation is the more appropriate body to impose punishment for that new criminal conduct, and that, as a breach of trust inherent in the conditions of supervision, the sanction for the violation of trust should be in addition, or consecutive, to any sentence imposed for the new conduct. In contrast, the second option would have the revocation court substantially duplicate the sanctioning role of the court with jurisdiction over a defendant’s new criminal conduct and would provide for the punishment imposed upon revocation to run concurrently with, and thus generally be subsumed in, any sentence imposed for that new criminal conduct.</p><p>Further, the sanctions available to the courts upon revocation are, in many cases, more significantly restrained by statute. Specifically, the term of imprisonment that may be imposed upon revocation of supervised release is limited by statute to not more than five years for persons convicted of Class A felonies, except for certain Title 21 drug offenses; not more than three years for Class B felonies; not more than two years for Class C or D felonies; and not more than one year for Class E felonies. 18 U.S.C. § 3583(e)(3).</p><p>Given the relatively narrow ranges of incarceration available in many cases, combined with the potential difficulty in obtaining information necessary to determine specific offense characteristics, the Commission felt that it was undesirable at this time to develop guidelines that attempt to distinguish, in detail, the wide variety of behavior that can lead to revocation. Indeed, with the relatively low ceilings set by statute, revocation policy statements that attempted to delineate with great particularity the gradations of conduct leading to revocation would frequently result in a sentence at the statutory maximum penalty.</p><p>Accordingly, the Commission determined that revocation policy statements that provided for three broad grades of violations would permit proportionally longer terms for more serious violations and thereby would address adequately concerns about proportionality, without creating the problems inherent in the second option.</p><p><strong>4. The Basic Approach</strong></p><p>The revocation policy statements categorize violations of probation and supervised release in three broad classifications ranging from serious new felonious criminal conduct to less serious criminal conduct and technical violations. The grade of the violation, together with the violator’s criminal history category calculated at the time of the initial sentencing, fix the applicable sentencing range.</p><p>The Commission has elected to develop a single set of policy statements for revocation of both probation and supervised release. In reviewing the relevant literature, the Commission determined that the purpose of supervision for probation and supervised release should focus on the integration of the violator into the community, while providing the supervision designed to limit further criminal conduct. Although there was considerable debate as to whether the sanction imposed upon revocation of probation should be different from that imposed upon revocation of supervised release, the Commission has initially concluded that a single set of policy statements is appropriate.</p><p><strong>5. A Concluding Note</strong></p><p>The Commission views these policy statements for revocation of probation and supervised release as the first step in an evolutionary process. The Commission expects to issue revocation guidelines after judges, probation officers, and practitioners have had an opportunity to apply and comment on the policy statements.</p><p>In developing these policy statements, the Commission assembled two outside working groups of experienced probation officers representing every circuit in the nation, officials from the Probation Division of the Administrative Office of the U.S. Courts, the General Counsel’s office at the Administrative Office of the U.S. Courts, and the U.S. Parole Commission. In addition, a number of federal judges, members of the Criminal Law and Probation Administration Committee of the Judicial Conference, and representatives from the Department of Justice and federal and community defenders provided considerable input into this effort.</p><table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1990 (amendment 362). Amended effective November 1, 2002 (amendment 646); November 1, 2009 (amendment 733).</td></tr></tbody></table><p><strong>§§7A1.1 – 7A1.4 [Deleted]</strong></p><table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Sections 7A1.1 (Reporting of Violations of Probation and Supervised Release), 7A1.2 (Revocation of Probation), 7A1.3 (Revocation of Supervised Release), and 7A1.4 (No Credit for Time Under Supervision), effective November 1, 1987, were deleted as part of an overall revision of this chapter effective November 1, 1990 (amendment 362).</td></tr></tbody></table>"},{"id":"B","type":"part","chapter":"7","title":"PROBATION AND SUPERVISED RELEASE VIOLATIONS","content":"<h4 id='introductory-commentary-18' class='Commentary'><strong>Introductory Commentary</strong></h4><p>The policy statements in this chapter seek to prescribe penalties only for the violation of the judicial order imposing supervision. Where a defendant is convicted of a criminal charge that also is a basis of the violation, these policy statements do not purport to provide the appropriate sanction for the criminal charge itself. The Commission has concluded that the determination of the appropriate sentence on any new criminal conviction should be a separate determination for the court having jurisdiction over such conviction.</p><p>Because these policy statements focus on the violation of the court-ordered supervision, this chapter, to the extent permitted by law, treats violations of the conditions of probation and supervised release as functionally equivalent.</p><p>Under 18 U.S.C. § 3584, the court, upon consideration of the factors set forth in 18 U.S.C. § 3553(a), including applicable guidelines and policy statements issued by the Sentencing Commission, may order a term of imprisonment to be served consecutively or concurrently to an undischarged term of imprisonment. It is the policy of the Commission that the sanction imposed upon revocation is to be served consecutively to any other term of imprisonment imposed for any criminal conduct that is the basis of the revocation.</p><p>This chapter is applicable in the case of a defendant under supervision for a felony or Class A misdemeanor. Consistent with §1B1.9 (Class B or C Misdemeanors and Infractions), this chapter does not apply in the case of a defendant under supervision for a Class B or C misdemeanor or an infraction.</p><table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1990 (amendment 362).</td></tr></tbody></table>"},{"id":"A","type":"part","chapter":"8","title":"GENERAL APPLICATION PRINCIPLES"},{"id":"B","type":"part","chapter":"8","title":"REMEDYING HARM FROM CRIMINAL CONDUCT, AND EFFECTIVE COMPLIANCE AND ETHICS PROGRAM","content":"<table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1991 (amendment 422). Amended effective November 1, 2004 (amendment 673).</td></tr></tbody></table>"},{"id":"C","type":"part","chapter":"8","title":"FINES"},{"id":"D","type":"part","chapter":"6","title":"ORGANIZATIONAL PROBATION","content":"<h4 id='introductory-commentary-22' class='Commentary'><strong>Introductory Commentary</strong></h4><p>Section 8D1.1 sets forth the circumstances under which a sentence to a term of probation is required. Sections 8D1.2 through 8D1.4, and 8F1.1, address the length of the probation term, conditions of probation, and violations of probation conditions.</p><table><tbody><tr class='odd'><td><em>Historical Note</em></td><td>Effective November 1, 1991 (amendment 422). Amended effective November 1, 2004 (amendment 673).</td></tr></tbody></table>"},{"id":"E","type":"part","chapter":"8","title":"SPECIAL ASSESSMENTS, FORFEITURES, AND COSTS"},{"id":"F","type":"part","chapter":"8","title":"VIOLATIONS OF PROBATION -ORGANIZATIONS"}]

